import * as $ from 'jquery';
import 'jquery-validation';
import { sendMessage, showOverlay } from './dialogs';
import { fabric } from './office-ui-fabric';
import { getUrlParams } from './utilities';

export interface Credentials {
  password: string;
  username: string;
}

interface Parameters {
  [key: string]: string;
  invalidCredentials: string;
  userName: string;
  serverUrl: string;
}

Office.initialize = () => {
  const $dialog = $('.app-Dialog');
  // tslint:disable:no-unused-expression
  $('.ms-Overlay').each((_, overlay) => void new fabric.Overlay(overlay));
  $('.ms-TextField').each((_, textField) => void new fabric.TextField(textField));
  $('.ms-Button').each((_, button) => void new fabric.Button(button));
  // tslint:enable:no-unused-expression

  const params = getUrlParams(window.location.search) as Parameters;
  $dialog.find('#app-ServerUrl').val(params.serverUrl);
  $dialog.find('#app-Username').val(params.userName);
  if (params.invalidCredentials === 'true') {
    $dialog.find('#app-InvalidCredentialsMessage').show();
  }
  $dialog.show();

  const validator = $dialog.validate({
    submitHandler: form => {
      showOverlay($dialog);
      setTimeout(() => {
        const $form = $(form);
        const credentials: Credentials = {
          password: $form.find('#app-Password').val() as string,
          username: $form.find('#app-Username').val() as string
        };

        validator.destroy();
        sendMessage('credentials', credentials);
      }, 0);
    }
  });

  $dialog.find('#app-CancelButton').on('click', () => {
    showOverlay($dialog);
    setTimeout(() => {
      validator.destroy();
      sendMessage('dialogClosed');
    }, 0);
  });
};
